@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
.fruit-card {
  position: relative;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  transition: all 0.3s ease;
  background: var(--card-face-up-bg);
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
}

.fruit-card.flip {
  transform: rotateY(180deg);
}
/* @keyframes matchingCardsScaled {
  from {
    transform: scale(1.05);
  }
  to {
    transform: scale(1);
  }
} */

.card-face-up {
  position: absolute;
  max-width: 100%;
  top: 50%;
  left: 50%;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: translate(-50%, -50%) rotateY(180deg) translate3d(0, 0, 1px);
  -webkit-transform: translate(-50%, -50%) rotateY(180deg)
    translate3d(0, 0, 1px);
  -moz-transform: translate(-50%, -50%) rotateY(180deg);
  -ms-transform: translate(-50%, -50%) rotateY(180deg);
  z-index: 1;
}

.card-face-down {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url(/memory-game-react/static/media/PurpleDottedBg.9e53a9ac.png);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  border-radius: 6px;
}

@media only screen and (min-width: 600px) {
  .card-face-up {
    max-width: 90%;
  }
}

@media only screen and (min-width: 900px) {
  .card-face-up {
    max-width: 70%;
  }
}

@media (orientation: landscape) and (max-width: 700px) {
  .card-face-up {
    max-width: 70%;
  }
}

.game-end-backdrop {
  width: 100%;
  height: 100%;
  z-index: 2;
  background: var(--end-ofthe-game-bg);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}
.game-end-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--font-color);
  background: var(--end-of-the-game-card);
  height: 35%;
  width: 75vw;
  -webkit-animation: gameEndCardAnimation 0.7s ease-in forwards;
          animation: gameEndCardAnimation 0.7s ease-in forwards;
}
@-webkit-keyframes gameEndCardAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes gameEndCardAnimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.game-end-card h2 {
  font-family: var(--font-family);
}

.play-again-btn {
  width: 11rem;
  height: 4rem;
  background: var(--navbar-bg);
  font-size: 1.2rem;
  margin-top: 2rem;
  border-style: solid;
  border-width: 0.2rem;
  border-image: linear-gradient(
    to top right,
    var(--progress-bar-filler-color),
    var(--font-color)
  );
  border-image-slice: 1;
}
.play-again-btn:hover {
  cursor: pointer;
  background-image: linear-gradient(
    to top right,
    var(--progress-bar-filler-color),
    var(--font-color)
  );
  color: var(--end-of-the-game-card);
}

@media (min-width: 600px) {
  .game-end-card {
    width: 60%;
  }
}
@media (min-width: 800px) {
  .game-end-card {
    width: 40%;
  }
}

.navigation-bar {
  width: 80vw;
  height: 3rem;
  background: var(--navbar-bg);
  position: relative;
  color: var(--font-color);
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 6px;
  font-family: var(--font-family);
}

.progress-bar {
  display: flex;
  align-items: center;
  width: 60%;
}
.progress-bar h5 {
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
}

.progress-bar-container {
  width: 100%;
  height: 1.5rem;
  border-radius: 8px;
  background: var(--card-face-up-bg);
  margin-left: 0.3rem;
}

.progress-bar-filler {
  height: 100%;
  background: var(--progress-bar-filler-color);
  border-radius: 8px;
  transition: width 0.2s ease-in-out;
}

@media only screen and (min-width: 701px) {
  .navigation-bar {
    width: 66vw;
  }
}

@media (orientation: landscape) and (max-width: 700px) {
  .navigation-bar {
    height: 2rem;
    width: 66vw;
  }
  .progress-bar-container {
    height: 1rem;
  }
}

.game-field-container {
  width: 80vw;
  height: 65vh;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 0.6rem;
  gap: 0.6rem;
  position: relative;
  perspective: 1000px;
}

@media only screen and (orientation: landscape) {
  .game-field-container {
    height: 55vh;
    width: 66vw;
    grid-template-columns: repeat(4, auto);
  }
}
@media only screen and (min-width: 800px) {
  .game-field-container {
    grid-gap: 1rem;
    gap: 1rem;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  --navbar-bg: #ffef62;
  --end-of-the-game-card: rgb(255, 251, 239);
  --end-ofthe-game-bg: rgb(253, 245, 218, 0.2);
  --font-color: #33091d;
  --card-face-up-bg: rgb(253, 245, 218);
  --progress-bar-filler-color: rgb(148, 163, 255);
  --font-family: "Poppins", sans-serif;
}
html {
  height: 100%;
  width: 100%;
}
body {
  height: 100%;
  width: 100%;
  margin: auto;
  display: flex;
  overflow: hidden;
}

.App {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

header {
  color: var(--font-color);
  font-family: var(--font-family);
}

@media (orientation: landscape) and (max-width: 700px) {
  header {
    font-size: smaller;
  }
}

